import { Type } from "@thi.ng/geom-api";
import { copyShape } from "../internal/copy-shape";
import { APC } from "./apc";
export class Triangle extends APC {
    get type() {
        return Type.TRIANGLE;
    }
    copy() {
        return copyShape(Triangle, this);
    }
    toHiccup() {
        return ["polygon", this.attribs, this.points];
    }
}
