import { isString } from "@thi.ng/checks";
import { resolveAsCSS } from "@thi.ng/color";
export const resolveColor = (v) => (isString(v) ? v : resolveAsCSS(v));
export const resolveGradientOrColor = (state, v) => isString(v)
    ? v[0] === "$"
        ? state.grads[v.substr(1)]
        : v
    : resolveAsCSS(v);
export const defLinearGradient = (ctx, { from, to }, stops) => {
    const g = ctx.createLinearGradient(from[0], from[1], to[0], to[1]);
    for (let s of stops) {
        g.addColorStop(s[0], resolveColor(s[1]));
    }
    return g;
};
export const defRadialGradient = (ctx, { from, to, r1, r2 }, stops) => {
    const g = ctx.createRadialGradient(from[0], from[1], r1, to[0], to[1], r2);
    for (let s of stops) {
        g.addColorStop(s[0], resolveColor(s[1]));
    }
    return g;
};
