import { defmulti } from "@thi.ng/defmulti";
import { IntersectionType, Type, } from "@thi.ng/geom-api";
import { intersectCircleCircle, intersectLineLine, intersectPlanePlane, intersectRayAABB, intersectRayCircle, intersectRayPlane, intersectRayPolyline, intersectRayRect, testRectCircle, testRectRect, } from "@thi.ng/geom-isec";
import { dispatch2 } from "../internal/dispatch";
export const intersects = defmulti(dispatch2);
intersects.addAll({
    [`${Type.CIRCLE}-${Type.CIRCLE}`]: (a, b) => intersectCircleCircle(a.pos, b.pos, a.r, b.r),
    [`${Type.LINE}-${Type.LINE}`]: ({ points: a }, { points: b }) => intersectLineLine(a[0], a[1], b[0], b[1]),
    [`${Type.PLANE}-${Type.PLANE}`]: (a, b) => intersectPlanePlane(a.normal, a.w, b.normal, b.w),
    [`${Type.RAY}-${Type.AABB}`]: (ray, box) => intersectRayAABB(ray.pos, ray.dir, box.pos, box.max()),
    [`${Type.RAY}-${Type.CIRCLE}`]: (ray, sphere) => intersectRayCircle(ray.pos, ray.dir, sphere.pos, sphere.r),
    [`${Type.RAY}-${Type.PLANE}`]: (ray, plane) => intersectRayPlane(ray.pos, ray.dir, plane.normal, plane.w),
    [`${Type.RAY}-${Type.POLYGON}`]: (ray, poly) => intersectRayPolyline(ray.pos, ray.dir, poly.points, true),
    [`${Type.RAY}-${Type.POLYLINE}`]: (ray, poly) => intersectRayPolyline(ray.pos, ray.dir, poly.points, false),
    [`${Type.RAY}-${Type.RECT}`]: (ray, rect) => intersectRayRect(ray.pos, ray.dir, rect.pos, rect.max()),
    [`${Type.RECT}-${Type.CIRCLE}`]: (rect, circle) => ({
        type: testRectCircle(rect.pos, rect.size, circle.pos, circle.r)
            ? IntersectionType.INTERSECT
            : IntersectionType.NONE,
    }),
    [`${Type.RECT}-${Type.RECT}`]: (a, b) => ({
        type: testRectRect(a.pos, a.size, b.pos, b.size)
            ? IntersectionType.INTERSECT
            : IntersectionType.NONE,
    }),
});
intersects.isa(`${Type.RAY}-${Type.SPHERE}`, `${Type.RAY}-${Type.CIRCLE}`);
intersects.isa(`${Type.RAY}-${Type.QUAD}`, `${Type.RAY}-${Type.POLYGON}`);
intersects.isa(`${Type.RAY}-${Type.TRIANGLE}`, `${Type.RAY}-${Type.POLYGON}`);
intersects.isa(`${Type.SPHERE}-${Type.SPHERE}`, `${Type.CIRCLE}-${Type.CIRCLE}`);
