import { centroid } from "@thi.ng/geom-poly-utils";
import { comp, map, partition, push, transduce, wrapSides, } from "@thi.ng/transducers";
import { mixN } from "@thi.ng/vectors";
export const quadFan = (points) => {
    const p = centroid(points);
    return transduce(comp(partition(3, 1), map(([a, b, c]) => [mixN([], a, b, 0.5), b, mixN([], b, c, 0.5), p])), push(), wrapSides(points));
};
