import { Type } from "@thi.ng/geom-api";
import { copyShape } from "../internal/copy-shape";
import { APC } from "./apc";
export class Quad3 extends APC {
    get type() {
        return Type.QUAD3;
    }
    copy() {
        return copyShape(Quad3, this);
    }
    toHiccup() {
        return ["polygon", this.attribs, this.points];
    }
}
