import { peek } from "@thi.ng/arrays";
import { defmulti } from "@thi.ng/defmulti";
import { SegmentType, Type } from "@thi.ng/geom-api";
import { simplify as _simplify } from "@thi.ng/geom-resample";
import { Path } from "../api/path";
import { Polygon } from "../api/polygon";
import { Polyline } from "../api/polyline";
import { copyAttribs } from "../internal/copy-attribs";
import { dispatch } from "../internal/dispatch";
import { vertices } from "./vertices";
export const simplify = defmulti(dispatch);
simplify.addAll({
    [Type.PATH]: ($, eps = 0.1) => {
        const res = [];
        const orig = $.segments;
        const n = orig.length;
        let points;
        let lastP;
        for (let i = 0; i < n; i++) {
            const s = orig[i];
            if (s.type === SegmentType.LINE ||
                s.type === SegmentType.POLYLINE) {
                points = points
                    ? points.concat(vertices(s.geo))
                    : vertices(s.geo);
                lastP = peek(points);
            }
            else if (points) {
                points.push(lastP);
                res.push({
                    geo: new Polyline(_simplify(points, eps)),
                    type: SegmentType.POLYLINE,
                });
                points = null;
            }
            else {
                res.push(Object.assign({}, s));
            }
        }
        if (points) {
            points.push(lastP);
            res.push({
                geo: new Polyline(points),
                type: SegmentType.POLYLINE,
            });
        }
        return new Path(res, copyAttribs($));
    },
    [Type.POLYGON]: ($, eps = 0.1) => new Polygon(_simplify($.points, eps, true), copyAttribs($)),
    [Type.POLYLINE]: ($, eps = 0.1) => new Polyline(_simplify($.points, eps), copyAttribs($)),
});
