import { defmulti } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { closestPoint as closestPointArc } from "@thi.ng/geom-arc";
import { closestPointAABB, closestPointArray, closestPointCircle, closestPointPlane, closestPointPolyline, closestPointRect, closestPointSegment, } from "@thi.ng/geom-closest-point";
import { closestPointCubic, closestPointQuadratic } from "@thi.ng/geom-splines";
import { add2, add3 } from "@thi.ng/vectors";
import { dispatch } from "../internal/dispatch";
export const closestPoint = defmulti(dispatch);
closestPoint.addAll({
    [Type.AABB]: ($, p, out) => closestPointAABB(p, $.pos, add3([], $.pos, $.size), out),
    [Type.ARC]: ($, p, out) => closestPointArc(p, $.pos, $.r, $.axis, $.start, $.end, out),
    [Type.CIRCLE]: ($, p, out) => closestPointCircle(p, $.pos, $.r, out),
    [Type.CUBIC]: ({ points }, p, out) => closestPointCubic(p, points[0], points[1], points[2], points[3], out),
    [Type.LINE]: ({ points }, p, out) => closestPointSegment(p, points[0], points[1], out),
    [Type.PLANE]: ($, p, out) => closestPointPlane(p, $.normal, $.w, out),
    [Type.POINTS]: ($, p, out) => closestPointArray(p, $.points, out),
    [Type.POLYGON]: ($, p, out) => closestPointPolyline(p, $.points, true, out),
    [Type.POLYLINE]: ($, p, out) => closestPointPolyline(p, $.points, false, out),
    [Type.QUADRATIC]: ({ points }, p, out) => closestPointQuadratic(p, points[0], points[1], points[2], out),
    [Type.RECT]: ($, p, out) => closestPointRect(p, $.pos, add2([], $.pos, $.size), out),
});
closestPoint.isa(Type.QUAD, Type.POLYGON);
closestPoint.isa(Type.SPHERE, Type.CIRCLE);
closestPoint.isa(Type.TRIANGLE, Type.POLYGON);
