import { illegalArgs } from "@thi.ng/errors";
import { add, divN, empty } from "@thi.ng/vectors";
export const centroid = (pts, out) => {
    const num = pts.length;
    !num && illegalArgs("no points");
    !out && (out = empty(pts[0]));
    for (let i = num; --i >= 0;) {
        add(out, out, pts[i]);
    }
    return divN(out, out, num);
};
