import { defmulti } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { Sampler } from "@thi.ng/geom-resample";
import { cossin, HALF_PI, TAU } from "@thi.ng/math";
import { direction } from "@thi.ng/vectors";
import { dispatch } from "../internal/dispatch";
import { vertices } from "./vertices";
export const tangentAt = defmulti(dispatch);
tangentAt.addAll({
    [Type.CIRCLE]: (_, t) => cossin(TAU * t + HALF_PI),
    [Type.LINE]: ({ points }) => direction([], points[0], points[1]),
    [Type.POLYGON]: ($, t) => new Sampler($.points, true).tangentAt(t),
    [Type.POLYLINE]: ($, t) => new Sampler($.points).tangentAt(t),
    [Type.RECT]: ($, t) => new Sampler(vertices($), true).tangentAt(t),
});
tangentAt.isa(Type.QUAD, Type.POLYGON);
tangentAt.isa(Type.TRIANGLE, Type.POLYGON);
