import { serialize } from "@thi.ng/hiccup";
import { convertTree, ff, svg } from "@thi.ng/hiccup-svg";
import { collBounds } from "../internal/coll-bounds";
import { bounds } from "./bounds";
export const asSvg = (...args) => args.map((x) => serialize(convertTree(x))).join("");
export const svgDoc = (attribs, ...xs) => {
    if (xs.length > 0) {
        if (!attribs || !attribs.viewBox) {
            const cbounds = collBounds(xs, bounds);
            if (cbounds) {
                const [[x, y], [w, h]] = cbounds;
                attribs = Object.assign({ width: ff(w), height: ff(h), viewBox: `${ff(x)} ${ff(y)} ${ff(w)} ${ff(h)}` }, attribs);
            }
        }
    }
    return svg(attribs, ...xs);
};
