import { centroid } from "@thi.ng/geom-poly-utils";
import { comp, map, partition, push, transduce, wrapSides, } from "@thi.ng/transducers";
export const triFan = (points) => {
    const c = centroid(points);
    return transduce(comp(partition(2, 1), map(([a, b]) => [a, b, c])), push(), wrapSides(points, 0, 1));
};
