import { Type } from "@thi.ng/geom-api";
import { copyShape } from "../internal/copy-shape";
import { APC } from "./apc";
export class Polyline extends APC {
    get type() {
        return Type.POLYLINE;
    }
    copy() {
        return copyShape(Polyline, this);
    }
    toHiccup() {
        return ["polyline", Object.assign(Object.assign({}, this.attribs), { fill: "none" }), this.points];
    }
    toHiccupPathSegments() {
        const res = [];
        for (let pts = this.points, n = pts.length, i = 1; i < n; i++) {
            res.push(["L", pts[i]]);
        }
        return res;
    }
}
