import { fit01, minError } from "@thi.ng/math";
import { distSq2 } from "@thi.ng/vectors";
import { pointAtTheta } from "./point-at";
export const closestPoint = (p, o, r, axis, start, end, out = [], res, iter) => {
    const fn = (t) => pointAtTheta(o, r, axis, fit01(t, start, end), out);
    return fn(minError(fn, distSq2, p, res, iter));
};
