import { defmulti } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { Sampler } from "@thi.ng/geom-resample";
import { cubicSplitAt, quadraticSplitAt } from "@thi.ng/geom-splines";
import { fit01 } from "@thi.ng/math";
import { set } from "@thi.ng/vectors";
import { Arc } from "../api/arc";
import { Cubic } from "../api/cubic";
import { Line } from "../api/line";
import { Polyline } from "../api/polyline";
import { Quadratic } from "../api/quadratic";
import { copyAttribs } from "../internal/copy-attribs";
import { dispatch } from "../internal/dispatch";
import { pointArraysAsShapes } from "../internal/points-as-shape";
import { splitLine } from "../internal/split";
export const splitAt = defmulti(dispatch);
splitAt.addAll({
    [Type.ARC]: ($, t) => {
        const theta = fit01(t, $.start, $.end);
        return [
            new Arc(set([], $.pos), set([], $.r), $.axis, $.start, theta, $.xl, $.cw, copyAttribs($)),
            new Arc(set([], $.pos), set([], $.r), $.axis, theta, $.end, $.xl, $.cw, copyAttribs($)),
        ];
    },
    [Type.CUBIC]: ({ attribs, points }, t) => cubicSplitAt(points[0], points[1], points[2], points[3], t).map((pts) => new Cubic(pts, Object.assign({}, attribs))),
    [Type.LINE]: ({ attribs, points }, t) => splitLine(points[0], points[1], t).map((pts) => new Line(pts, Object.assign({}, attribs))),
    [Type.POLYLINE]: ($, t) => pointArraysAsShapes(Polyline, new Sampler($.points).splitAt(t), $.attribs),
    [Type.QUADRATIC]: ({ attribs, points }, t) => quadraticSplitAt(points[0], points[1], points[2], t).map((pts) => new Quadratic(pts, Object.assign({}, attribs))),
});
