import { defmulti } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { perimeter } from "@thi.ng/geom-poly-utils";
import { PI, TAU } from "@thi.ng/math";
import { dist } from "@thi.ng/vectors";
import { dispatch } from "../internal/dispatch";
/**
 * Returns the arc length / perimeter / circumference of the given
 * shape. For groups calls {@link arcLength} for each child and returns the
 * sum of results.
 *
 * Implemented for:
 *
 * - Circle
 * - Ellipse
 * - Group
 * - Line
 * - Polygon
 * - Polyline
 * - Quad
 * - Rect
 * - Triangle
 *
 */
export const arcLength = defmulti(dispatch);
arcLength.addAll({
    [Type.CIRCLE]: ($) => TAU * $.r,
    [Type.ELLIPSE]: ({ r: [a, b] }) => 
    // Ramanujan approximation
    // https://www.mathsisfun.com/geometry/ellipse-perimeter.html
    PI * (3 * (a + b) - Math.sqrt((3 * a + b) * (3 * b + a))),
    [Type.GROUP]: ({ children }) => children.reduce((sum, $) => sum + arcLength($), 0),
    [Type.LINE]: ({ points }) => dist(points[0], points[1]),
    [Type.POLYGON]: ({ points }) => perimeter(points, points.length, true),
    [Type.POLYLINE]: ({ points }) => perimeter(points, points.length),
    [Type.RECT]: ({ size }) => 2 * (size[0] + size[1]),
    [Type.TRIANGLE]: ({ points }) => dist(points[0], points[1]) +
        dist(points[1], points[2]) +
        dist(points[2], points[0]),
});
arcLength.isa(Type.QUAD, Type.POLYGON);
arcLength.isa(Type.TRIANGLE, Type.POLYGON);
