import { endShape } from "./end-shape";
export const polygon = (ctx, attribs, pts) => {
    if (pts.length < 2)
        return;
    __drawPoly(ctx, pts);
    ctx.closePath();
    endShape(ctx, attribs);
};
/**
 * Shared internal helper for polygon & polyline fns.
 *
 * @param ctx - canvas context
 * @param pts - poly vertices
 */
export const __drawPoly = (ctx, pts) => {
    let p = pts[0];
    ctx.beginPath();
    ctx.moveTo(p[0], p[1]);
    for (let i = 1, n = pts.length; i < n; i++) {
        p = pts[i];
        ctx.lineTo(p[0], p[1]);
    }
};
