import { DEFAULT, defmulti, } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { set2, set3, submN, ZERO2, ZERO3 } from "@thi.ng/vectors";
import { Arc } from "../api/arc";
import { Circle } from "../api/circle";
import { Ellipse } from "../api/ellipse";
import { Sphere } from "../api/sphere";
import { copyAttribs } from "../internal/copy-attribs";
import { dispatch } from "../internal/dispatch";
import { centroid } from "./centroid";
import { translate } from "./translate";
export const center = defmulti(dispatch);
center.add(DEFAULT, ($, origin = ZERO3) => {
    const c = centroid($);
    return c ? translate($, submN(null, c, origin, -1)) : undefined;
});
center.addAll({
    [Type.ARC]: ($, origin = ZERO2) => new Arc(set2([], origin), set2([], $.r), $.axis, $.start, $.end, $.xl, $.cw, copyAttribs($)),
    [Type.CIRCLE]: ($, origin = ZERO2) => new Circle(set2([], origin), $.r, copyAttribs($)),
    [Type.ELLIPSE]: ($, origin = ZERO2) => new Ellipse(set2([], origin), set2([], $.r), copyAttribs($)),
    [Type.SPHERE]: ($, origin = ZERO3) => new Sphere(set3([], origin), $.r, copyAttribs($)),
});
