import { DEFAULT, defmulti } from "@thi.ng/defmulti";
import { illegalArgs } from "@thi.ng/errors";
import { ColorMode } from "./constants";
import { convert } from "./convert";
import { luminanceInt, luminanceRGB } from "./luminance-rgb";
/**
 * Multi-method to compute relative luminance from any supported input
 * color format. Unless color already is an RGBA or ARGB int (plain or
 * wrapped), it will first be converted to RGBA.
 */
export const luminance = defmulti((col, mode) => col.mode !== undefined
    ? col.mode
    : mode !== undefined
        ? mode
        : illegalArgs(`missing color mode`));
luminance.add(ColorMode.RGBA, (x) => luminanceRGB(x));
luminance.add(ColorMode.INT32, (x) => luminanceInt(typeof x === "number" ? x : x.deref()));
luminance.add(DEFAULT, (x, mode) => luminanceRGB(convert(x, ColorMode.RGBA, mode)));
