import { DEFAULT, defmulti, } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { polyArea2 } from "@thi.ng/geom-poly-utils";
import { PI } from "@thi.ng/math";
import { signedArea2 } from "@thi.ng/vectors";
import { dispatch } from "../internal/dispatch";
/**
 * Returns the possibly signed (unsigned by default) surface area of given
 * `shape`. For groups calls {@link area} for each child and returns sum of
 * unsigned areas.
 *
 * In general, for polygons and triangles, the sign of the result can be
 * used as indication of the shapes orientation (clockwise /
 * counterclockwise).
 *
 * For curves, lines, point clouds and rays the function returns 0.
 *
 * Implemented for:
 *
 * - AABB
 * - Circle
 * - Cubic
 * - Ellipse
 * - Group
 * - Line
 * - Plane
 * - Points
 * - Polygon
 * - Polyline
 * - Quad
 * - Quadratic
 * - Ray
 * - Rect
 * - Sphere
 * - Triangle
 *
 * @param shape - shape to operate on
 * @param signed - true, if signed area
 */
export const area = defmulti(dispatch);
area.add(DEFAULT, () => 0);
area.addAll({
    [Type.AABB]: ({ size: [w, h, d] }) => 2 * (w * h + w * d + h * d),
    [Type.ARC]: 
    // http://cut-the-knot.org/Generalization/Cavalieri2.shtml
    ($) => 0.5 * Math.abs($.start - $.end) * $.r[0] * $.r[1],
    [Type.CIRCLE]: ($) => PI * $.r ** 2,
    [Type.ELLIPSE]: ($) => PI * $.r[0] * $.r[1],
    [Type.GROUP]: ({ children }) => children.reduce((sum, $) => sum + area($, false), 0),
    [Type.PLANE]: () => Infinity,
    [Type.POLYGON]: ($, signed) => {
        const area = polyArea2($.points);
        return signed ? area : Math.abs(area);
    },
    [Type.RECT]: ($) => $.size[0] * $.size[1],
    [Type.SPHERE]: ($) => 4 * PI * $.r ** 2,
    [Type.TRIANGLE]: ($, signed) => {
        const area = 0.5 * signedArea2(...$.points);
        return signed ? area : Math.abs(area);
    },
});
area.isa(Type.QUAD, Type.POLYGON);
