import { DEFAULT, defmulti } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { PI } from "@thi.ng/math";
import { dispatch } from "../internal/dispatch";
/**
 * Returns the volume of given 3D shape. Returns 0 for all others.
 *
 * Currently only implemented for:
 *
 * - AABB
 * - Sphere
 */
export const volume = defmulti(dispatch);
volume.add(DEFAULT, () => 0);
volume.addAll({
    [Type.AABB]: ({ size }) => size[0] * size[1] * size[2],
    [Type.SPHERE]: ($) => (4 / 3) * PI * $.r ** 3,
});
