import { defmulti } from "@thi.ng/defmulti";
import { Type } from "@thi.ng/geom-api";
import { Sampler } from "@thi.ng/geom-resample";
import { cossin, fit01, TAU } from "@thi.ng/math";
import { cartesian2, madd2, mixCubic, mixN2, mixQuadratic, pointOnRay2, pointOnRay3, } from "@thi.ng/vectors";
import { dispatch } from "../internal/dispatch";
import { vertices } from "./vertices";
export const pointAt = defmulti(dispatch);
pointAt.addAll({
    [Type.ARC]: ($, t) => $.pointAtTheta(fit01(t, $.start, $.end)),
    [Type.CIRCLE]: ($, t) => cartesian2(null, [$.r, TAU * t], $.pos),
    [Type.CUBIC]: ({ points }, t) => mixCubic([], points[0], points[1], points[2], points[3], t),
    [Type.ELLIPSE]: ($, t) => madd2([], cossin(TAU * t), $.r, $.pos),
    [Type.LINE]: ({ points }, t) => mixN2([], points[0], points[1], t),
    [Type.POLYGON]: ($, t) => new Sampler($.points, true).pointAt(t),
    [Type.POLYLINE]: ($, t) => new Sampler($.points).pointAt(t),
    [Type.QUADRATIC]: ({ points }, t) => mixQuadratic([], points[0], points[1], points[2], t),
    [Type.RAY]: ($, t) => pointOnRay2([], $.pos, $.dir, t),
    [Type.RAY3]: ($, t) => pointOnRay3([], $.pos, $.dir, t),
    [Type.RECT]: ($, t) => new Sampler(vertices($), true).pointAt(t),
});
pointAt.isa(Type.QUAD, Type.POLYGON);
pointAt.isa(Type.TRIANGLE, Type.POLYGON);
