import { ColorMode } from "./constants";
export const css = (col) => new CSS(col);
export class CSS {
    constructor(col) {
        this.value = col;
    }
    get mode() {
        return ColorMode.CSS;
    }
    copy() {
        return new CSS(this.value);
    }
    deref() {
        return this.value;
    }
}
